import React, {forwardRef} from "react";
import {
  Bar,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  ResponsiveContainer,
  ComposedChart,
  Tooltip,
} from "recharts";

function TestChart( props, ref ) {

  const data = props.data


  return (
    <ResponsiveContainer ref={ref}>
      <ComposedChart
        
        data={data}
        margin={{
          top: 10,
          right: 30,
          left: 30,
          bottom: 10,
        }}
      >
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="date" />
        <YAxis
          yAxisId="left"
          orientation="left"
          stroke="#2089C6"
          tickFormatter={(item) =>
            item.toLocaleString("de-DE", {
              style: "currency",
              currency: "EUR",
              maximumFractionDigits: 0,
            })
          }
        />
        <YAxis
          yAxisId="right"
          orientation="right"
          stroke="#30343F"
          tickFormatter={(item) =>
            item.toLocaleString("de-DE", {
              style: "currency",
              currency: "EUR",
              maximumFractionDigits: 0,
            })
          }
        />
        {/* <Legend /> */}
        <Bar yAxisId="left" dataKey="costs" fill="#2089C6" too/>
        <Bar yAxisId="left" dataKey="income" fill="#519599" />
        <Tooltip
            formatter={(value) => value.toLocaleString("de-DE", {
              style: "currency",
              currency: "EUR",
              maximumFractionDigits: 0,
            })}
          />
        <Line
          yAxisId="right"
          type="monotone"
          dataKey="balance"
          stroke="#30343F"
        />
      </ComposedChart>
    </ResponsiveContainer>
  );
}

export default forwardRef(TestChart);
